import React from 'react'
import styles from './index.module.less'
import { Button } from 'antd-mobile'
import { useNavigate } from 'react-router-dom'
const item = [
  { id: 1, url: '/miao', title: '喵语转换器' },
  { id: 2, url: '/qrcode', title: '二维码生成' },
  { id: 3, url: '/wallpaper', title: '随机壁纸' },
  { id: 4, url: '/vparse', title: '抖音视频解析' },
]

export default function Home() {
  const navigate = useNavigate()
  return (
    <div className={styles.container}>
      <p className={styles.title}>工具箱</p>
      {
        item.map(i => (
          <Button key={i.id} block shape='rounded' onClick={() => { navigate(i.url) }}>{i.title}</Button>
        ))
      }
      <p className={styles.me}>design by Hui,更多功能开发中···<span onClick={() => { navigate('/feed') }} style={{ color: '#1677ff', cursor: 'pointer' }}>投喂</span></p>
    </div>
  )
}
